@media (min-width: 1024px) {
  .footer {
    display: grid;
    padding-top: 4rem;
    padding-bottom: 5rem;
    grid-template-columns:
      1fr
      1fr
      1fr
      1fr
      1fr
      1fr
      1fr
      1fr
      1fr
      1fr;
    justify-items: start;
  }
  h3 {
    color: black;
  }

  a {
    color: darkgray;
    display: block;
    font-size: 0.8rem;
    margin-bottom: 10px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }

  a:hover {
    color: black;
  }

  .footer_lookbook {
    flex-direction: column;

    grid-column: 3;
  }

  .footer_nxt {
    flex-direction: column;

    grid-column: 4;
  }

  .footer_findus {
    flex-direction: column;
    grid-column: 5;
  }
  .footer_form {
    flex-direction: row;
    grid-column: 7;
    justify-content: space-between;
  }

  .inputField {
    border-bottom: 1px solid lightgray;
    border-top: none;
    border-left: none;
    border-right: none;
    outline: none;
    height: 30px;
    grid-column: 8;
    width: 100%;
    transition: 0.3s ease-in-out;
  }

  .inputField:focus {
    border-bottom: 1px solid black;
  }

  .footerButton {
    margin-top: 24px;
    border: 1px solid black;
    color: white;
    width: 100%;
    outline: none;
    background-color: black;
    height: 30px;
    border-radius: 24px;
    transition: 0.3s ease-in-out;
  }

  .footerButton:hover {
    border: 1px solid lightgray;
    background-color: white;
    color: black;
  }
}

.footer {
  border-top: 0.025rem solid lightgray;
}

@media (max-width: 1024px) {
  .footer {
    display: grid;
    padding-top: 2rem;
    padding-bottom: 5rem;

    justify-content: center;
  }
  h3 {
    color: black;
    font-size: 1.5rem;
  }

  h6 {
    color: darkgray;
    font-size: 0.8rem;
  }

  .footer_lookbook {
    flex-direction: column;
  }

  .footer_nxt {
    flex-direction: column;
  }

  .footer_findus {
    flex-direction: column;
  }
  .footer_form {
    flex-direction: row;

    justify-content: space-between;
  }

  .inputField {
    border-bottom: 1px solid lightgray;
    border-top: none;
    border-left: none;
    border-right: none;
    outline: none;
    height: 30px;
    grid-column: 8;
    width: 100%;
    transition: 0.3s ease-in-out;
  }

  .inputField:focus {
    border-bottom: 1px solid black;
  }
}
